@import '../vars';


html, body, #root {
    width: 100vw;
    margin: 0;
    overflow-x: hidden;
}

h1 {
    font-family: Inter, sans-serif;
    font-weight: 300;
    font-size: 2.25em;
}
h2 {
    font-family: Inter, sans-serif;
    font-weight: 300;
    font-size: 2em;
}
h3 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1.75em;
}
h4 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1.5em;
}
h5 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1.25em;
} 
h6 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1em;
    text-transform: uppercase;
}

p {
    font-family: Nunito, sans-serif;
    font-size: 1.25em
}

html {

    color: $cd-black;
    background-color: $cd-white;
    
    a:link {
        color: $cd-blue-700
    }
    a:visited {
        color: $cd-blue-900
    }
    a:hover, a:active {
        color: $cd-blue-500
    }

    ::selection {
        background-color: $cd-blue-200;
    }

    @media (prefers-color-scheme: dark) {
        color: $cd-white;
        background-color: $cd-black;


        a:link {
            color: $cd-blue-400
        }
        a:visited {
            color: $cd-blue-600
        }
        a:hover, a:active {
            color: $cd-blue-300
        }
        
        ::selection {
            background-color: $cd-blue-500;
        }

    }
}

.content-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;
    
    @media only screen and (max-width: 1000px) {
        max-width: 90%;
    }
    
    @media only screen and (min-width: 1000px) {
        max-width: 80%;
    }

    @media only screen and (min-width: 1600px) {
        max-width: 1280px;
    }
}

