html,body,#root{width:100vw;margin:0;overflow-x:hidden}h1{font-family:Inter, sans-serif;font-weight:300;font-size:2.25em}h2{font-family:Inter, sans-serif;font-weight:300;font-size:2em}h3{font-family:Inter, sans-serif;font-weight:700;font-size:1.75em}h4{font-family:Inter, sans-serif;font-weight:700;font-size:1.5em}h5{font-family:Inter, sans-serif;font-weight:700;font-size:1.25em}h6{font-family:Inter, sans-serif;font-weight:700;font-size:1em;text-transform:uppercase}p{font-family:Nunito, sans-serif;font-size:1.25em}html{color:#000008;background-color:#FDFDFF}html a:link{color:#284E87}html a:visited{color:#0F255A}html a:hover,html a:active{color:#5086bc}html ::selection{background-color:#BFE4F8}@media (prefers-color-scheme: dark){html{color:#FDFDFF;background-color:#000008}html a:link{color:#7AADD6}html a:visited{color:#3A68A1}html a:hover,html a:active{color:#99CAEA}html ::selection{background-color:#5086bc}}.content-container{display:flex;align-items:center;justify-content:center}.content{position:relative;flex:1 1 auto}@media only screen and (max-width: 1000px){.content{max-width:90%}}@media only screen and (min-width: 1000px){.content{max-width:80%}}@media only screen and (min-width: 1600px){.content{max-width:1280px}}

.card{display:grid;padding:1em 4em;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin-bottom:5em;box-shadow:0em 0 0  #999;transition:box-shadow .3s}@media only screen and (max-width: 1200px){.card{grid:"name" 3em "content" "space" 1em "images" 1fr / 1fr;padding:1em 2em}}@media only screen and (min-width: 1200px){.card{grid:"name images" 3em "content images" auto / 2fr 3fr;grid-column-gap:3em;-webkit-column-gap:3em;column-gap:3em;grid-row-gap:1em;row-gap:1em;padding:1em 4em}}@media only screen and (max-width: 1200px){.card h2{text-decoration:underline}}.card p{margin-left:1em}.card .images{grid-area:images;max-width:100%;max-height:100%}@media only screen and (max-width: 1200px){.card .images{place-self:stretch center}}@media only screen and (min-width: 1200px){.card .images{place-self:center end}}.card .images img{max-width:100%;max-height:30em}.card:hover{box-shadow:0em 0.5em 1em #999;cursor:pointer}

.stripes{position:absolute;left:0;top:0;bottom:0;display:flex}.stripes .s1,.stripes .s2,.stripes .s3{box-shadow:0 0 0.4em #09142D;transition:flex 0.1s;flex:0 0 .8em}.stripes .s1:hover,.stripes .s2:hover,.stripes .s3:hover{flex:0 0 1.2em}.stripes .s1{background-color:#7AADD6;z-index:1}.stripes .s2{background-color:#3A68A1;z-index:2}.stripes .s3{background-color:#19366D;z-index:3}.header-container{position:relative;overflow:hidden;max-width:100%;width:100%}@media only screen and (max-width: 1200px){.header-container{margin-bottom:2em}}@media only screen and (min-width: 1200px){.header-container{margin-bottom:5em}}.header{margin-left:4em;margin-right:1em;display:grid;flex:1 0 auto;max-width:100%}@media only screen and (max-width: 600px){.header{grid-template:"name" "email" "twitter" / auto}.header p{margin-top:0}}@media only screen and (min-width: 600px){.header{grid-template:"name email" "name twitter" / auto auto}.header p{margin-top:default}}@media only screen and (min-width: 800px){.header{grid-template:"name email twitter" / auto auto auto}.header p{margin-top:default}}.header *{justify-self:left;align-self:center}.header h1 a{text-decoration:none;color:#000008}.header h1 a :link{color:#000008}.header h1 a :visited{color:#000008}@media (prefers-color-scheme: dark){.header h1 a{color:#FDFDFF}.header h1 a :link{color:#FDFDFF}.header h1 a :visited{color:#FDFDFF}}

