@import '../vars';

.card {
    display: grid;    
    padding: 1em 4em;
    
    user-select: none;
    

    @media only screen and (max-width: 1200px) {
        grid:
            "name" 3em
            "content"
            "space" 1em
            "images" 1fr
            / 1fr;

        padding: 1em 2em;
    }

    
    @media only screen and (min-width: 1200px) {
        grid:
            "name images" 3em
            "content images" auto
            / 2fr 3fr;
        column-gap: 3em;
        row-gap: 1em;

        
        padding: 1em 4em;
    }

    margin-bottom: 5em;

    @media only screen and (max-width: 1200px) {
        h2 {
            text-decoration: underline;
        }
    }

    p {
        margin-left: 1em;
    }
    
    .images {
        grid-area: images;

        @media only screen and (max-width: 1200px) {
            place-self: stretch center;        
        }

        @media only screen and (min-width: 1200px) {
            place-self: center end;        
        }

        max-width: 100%;
        max-height: 100%;


        img {
            max-width: 100%;
            max-height: 30em;
        }
    }

    box-shadow: 0em 0 0  #999;
    transition: box-shadow .3s;
    &:hover {
        box-shadow: 0em 0.5em 1em #999;
        cursor: pointer;
    }
}


